@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
    width: 100vw;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    color: white;
    background: linear-gradient(#FFA37D, #FF627C) no-repeat
}

* {
  margin: 0;
  padding: 0;
}



.mainContainer {
    width: 100vw;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.mainContainer > h1 {
    font-size: 10vw;
}

.timerContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    max-width: 90vw;
    overflow: hidden;
}

.timerContainer .doublePoints {
    text-shadow: 0 5px 5px rgba(53, 53, 53, .2);
    font-size: 10vw;
}

.numberContainer {
    width: calc(90vw / 4);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
}

.numberContainer>div{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.numberContainer>h1 {
    text-shadow: 0 5px 5px rgba(53, 53, 53, .2);
    font-size: 15vw;
}

.flipContainer {
    overflow: hidden;
}

.flipContainer>h1 {
    text-shadow: 0 5px 5px rgba(53, 53, 53, .2);
    font-size: 15vw;
}

@media all and (min-width: 960px) {
    .mainContainer > h1 {
        font-size: 100px;
    }
    .numberContainer {
        margin: 30px;
    }
}

@media all and (max-width: 600px) {
    .numberContainer {
        margin: 5px;
    }
    .numberContainer>p {
        font-size: 10px;
    }
}
